<template>
	<div>
	  <el-button @click="getMockData"></el-button>
	  <el-button type="primary"></el-button>
	  <el-button type="success"></el-button>
	  <el-button type="info"></el-button>
	  <el-button type="warning"></el-button>
	  <el-button type="danger"></el-button>
	</div>
</template>

<script>
import axios from 'axios';


export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    getMockData () {
      let params = {a:1,b:'get'};
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/api/v1',
        url: '/mock/get',
        // to methods of that instance.
        method: 'get',
        // paramsget url
        params: params
      };
      axios.request(config)
      .then(result => {
          let data = result.data;
        console.log(data);
      }, err => {
        console.log(err);
      });
    }
  }
}

</script>
<style>
</style>


